/* TextChat用 CSS */
/* ここから */
.text-chat {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.text-chat-input {
  display: flex;
  justify-content: space-between;
  gap: 0.3rem;
}

.text-chat-input select {
  flex: 0 1 auto;
}

.text-chat-input input {
  flex: 1 1 auto;
}

.text-chat-input button {
  flex: 0 1 auto;
}

.text-chat-message-list-container {
  height: 12rem;
  border: 1px dotted grey;
  overflow: auto;
}

.text-chat-message-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: 100%;
  padding: 0 0.2rem;
}

.text-chat-from-me {
  display: flex;
  justify-content: flex-end;
}

.text-chat-from-them {
  display: flex;
  justify-content: flex-start;
}

.text-chat .text-chat-message-list-container .text-chat-message-list img {
  height: 2rem;
  border: 1px dotted grey;
}

/* TextChat.css */
.text-chat-message-list .text-chat-message {
  position: relative;
}

.reaction-buttons {
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
}

.text-chat-message-list .text-chat-message:hover .reaction-buttons {
  display: block;
}

/* TextChat.css */
.text-chat-message-list .text-chat-message {
  position: relative;
}

.reaction-buttons {
  display: none;
  position: absolute;
  bottom: -30px;
  left: 0;
}

.text-chat-message-list .text-chat-message:hover .reaction-buttons {
  display: block;
}

.reaction-mark {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: row-reverse;
}



/* ここまで */